body {
  margin: 0;
  line-height: normal;
}

:root {
  /* fonts */
  --px-body-mark-bold: "Mark for HCA";
  --icon-default-24px: "Material Icons";

  /* font sizes */
  --px-heading-5-mark-bold-size: 22px;
  --px-body-mark-bold-size: 16px;
  --icon-default-24px-size: 24px;
  --font-size-7xl: 26px;
  --px-caption-mark-regular-size: 14px;
  --button-small-size: 12px;
  --button-large-size: 18px;

  /* Colors */
  --system-plain-plain-0: #fff;
  --system-gray-gray-40: #939393;
  --system-greys-01-asphalt: #1f2532;
  --system-blue-blue-100: #06173d;
  --primary-colors-03-cerulean: #0085ca;
  --hca-primary-primary-60: #2075ad;
  --system-gray-gray-0: #f6f6f6;
  --color-black: #000;
  --color-dimgray: #666;
  --hca-secondary-secondary-60: #b7502b;
  --system-yellows-03-goldenrod: #ffc845;
  --hca-primary-primary-10: #d3e5f2;
  --color-lavender-100: rgba(244, 227, 252, 0);
  --color-mediumpurple: rgba(153, 104, 176, 0);
  --hca-primary-primary-90: #093865;

  /* Gaps */
  --gap-3xs: 10px;
  --gap-mini: 15px;
  --gap-5xs: 8px;
  --gap-8xs: 5px;

  /* Paddings */
  --padding-6xs: 7px;
  --padding-base: 16px;
  --padding-5xs: 8px;
  --padding-9xs: 4px;

  /* border radiuses */
  --br-8xs: 5px;

  /* Effects */
  --elevation-level-1: 0px 2px 4px rgba(25, 25, 25, 0.2);
  --elevation: 0px 10px 16px rgba(31, 37, 50, 0.15);
}
